import { Component, OnInit } from '@angular/core';
import { GetUserService } from '../get-user.service';
import { posts_restro } from '../posts_restro';

@Component({
  selector: 'app-restro',
  templateUrl: './restro.component.html',
  styleUrls: ['./restro.component.css']
})
export class RestroComponent implements OnInit {

  DATA : any;
  post2 = new posts_restro("" , "" , "","","","");

  constructor( private service : GetUserService ) { }

  ngOnInit() {
  }

  Onsubmit()
  {
    this.service.post2_restro(this.post2).subscribe(
      data => console.log("SUCCESS" , data) ,
      error => console.log("ERROR" , error)
    )
    location.assign('/loginRestro');

  }

}

