import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { posts } from './posts';
import { posts_restro } from './posts_restro';

@Injectable({
  providedIn: 'root'
})
export class GetUserService {

  constructor(private http : HttpClient) { }

  get1_user(username){
    return this.http.get('http://localhost:2019/users/get_user/'+username);
  }
  get2_user(username){
    return this.http.get('http://localhost:2019/restro/get_restro/'+username);
  }

  post1_user( posts : posts)
  {
    return this.http.post<any>('http://localhost:2019/users/post1' , posts , { headers : new HttpHeaders( { 'Content-Type' : 'application/json' } )});
  }
  post2_restro( posts_restro : posts_restro)
  {
    return this.http.post<any>('http://localhost:2019/restro/post2' , posts_restro , { headers : new HttpHeaders( { 'Content-Type' : 'application/json' } )});
  }

  put_user(user)
  {
   const headers = new HttpHeaders({Authorization : sessionStorage.getItem('basicauth')}); 
    return this.http.put<any>('http://localhost:2019/users/put_user' , user , {headers} );
  }
  put_restro(user)
  {
   const headers = new HttpHeaders({Authorization : sessionStorage.getItem('basicauth')}); 
    return this.http.put<any>('http://localhost:2019/restro/put_restro' , user , {headers} );
  }
}

